import { Box } from '@mui/material';
import { BodyScreenLayoutProps } from '../layout';

const BodyScreen: React.FC<BodyScreenLayoutProps> = function (props) {
  return (
    <Box
      sx={[
        { maxWidth: 1600, display: 'flex', margin: '0 auto' },
        ...(Array.isArray(props.sx) ? props.sx : [props.sx]),
      ]}
    >
      {props.children}
    </Box>
  );
};

export default BodyScreen;
