import * as React from 'react';
import { TextSpecialBoxProps } from './specialbox.interface';
import { SpecialBoxDefault } from './special-box-default';
import { Typography } from '@mui/material';

export const SpecialBoxText: React.FC<TextSpecialBoxProps> = (
  props: TextSpecialBoxProps
) => {
  const { component, placeholderNode, loadingNode, errorNode, ...rest } = props;
  return (
    <SpecialBoxDefault
      component={component || 'span'}
      placeholderNode={<Typography variant="inherit"></Typography>}
      loadingNode={<Typography variant="inherit">loading</Typography>}
      errorNode={<Typography variant="inherit"></Typography>}
      {...rest}
    />
  );
};
