import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import {
  MainLayoutOutlet,
  MainWithoutSidebar as MainLayout,
  wrapperLazyComponent,
} from "../layouts";
import Home from "@/views/home";
import H5NotSupported from "@/views/public/not-supported";
import { isMirco } from "@/utils/mirco";

const ShadowHome = React.lazy(() => import("@/views/shadows/shadow-home"));
const Shadow = React.lazy(() => import("@/views/shadows/shadow"));
const ShadowPlay = React.lazy(() => import("@/views/shadows/shadow-play"));
const ShadowUpload = React.lazy(() => import("@/views/shadows/shadow-upload"));
const ShadowUpdate = React.lazy(() => import("@/views/shadows/shadow-update"));
const Profile = React.lazy(() => import("@/views/users/profile"));
const Personal = React.lazy(() => import("@/views/users/personal"));
const Resume = React.lazy(() => import("@/views/users/resume"));
const Login = React.lazy(() => import("@/views/auth/login"));
const Completion = React.lazy(() => import("@/views/auth/completion"));
const NotFound = React.lazy(() => import("@/views/public/not-found"));
const Forbidden = React.lazy(() => import("@/views/public/forbidden"));
const InternalServerError = React.lazy(
  () => import("@/views/public/internal-server")
);
const Search = React.lazy(() => import("@/views/public/search"));

function AppRoute() {
  const isH5 = /Android|webOS|iPhone|iPod|BlackBerry/i.test(
    navigator.userAgent
  );

  if (isH5 && !window.location.pathname.includes("/not-support")) {
    window.location.href = window.location.origin + "/not-support";
  }

  return (
    <BrowserRouter basename={isMirco ? "/apps/miss" : "/"}>
      <Routes>
        <Route path="/" element={<MainLayoutOutlet layout={MainLayout} />}>
          <Route path="/" element={<Home />}></Route>
          <Route path="home" element={<Home />} />
          <Route
            path="shadow-home"
            element={wrapperLazyComponent(ShadowHome)}
          />
          <Route path="shadow/:id" element={wrapperLazyComponent(Shadow)} />
          <Route
            path="shadow-play/:id"
            element={wrapperLazyComponent(ShadowPlay)}
          />
          <Route
            path="shadow-upload"
            element={wrapperLazyComponent(ShadowUpload)}
          />
          <Route
            path="shadow-update/:id"
            element={wrapperLazyComponent(ShadowUpdate)}
          />
          <Route
            path="profile/:username"
            element={wrapperLazyComponent(Profile)}
          />
          <Route path="personal" element={wrapperLazyComponent(Personal)} />
          <Route path="search/:word" element={wrapperLazyComponent(Search)} />
        </Route>
        <Route path="/resume" element={wrapperLazyComponent(Resume)} />
        <Route path="/login" element={wrapperLazyComponent(Login)} />
        <Route path="/completion" element={wrapperLazyComponent(Completion)} />
        <Route path="/403" element={wrapperLazyComponent(Forbidden)} />
        <Route path="/404" element={wrapperLazyComponent(NotFound)} />
        <Route
          path="/50x"
          element={wrapperLazyComponent(InternalServerError)}
        />

        <Route path="/not-support" element={<H5NotSupported />} />
        <Route path="*" element={wrapperLazyComponent(NotFound)} />
      </Routes>
    </BrowserRouter>
  );
}

export default AppRoute;
