import * as React from "react";
import { Box, Typography, IconButton, Collapse, Divider } from "@mui/material";
import { KeyboardArrowLeft, KeyboardArrowRight } from "@mui/icons-material";
import { MediaNormal } from "@/components/app/media";
import SwipeableViews from "react-swipeable-views";
import { virtualize } from "react-swipeable-views-utils";
import { mod } from "react-swipeable-views-core";
import { useCurrentTopicQuery } from "@/schema";
import { SpecialBox } from "@/components/public/special-box";
import { Placeholder } from "@/components/base/placeholder";
import { useNavigate } from "react-router-dom";

const VirtualizeSwipeableViews = virtualize(SwipeableViews);

function Topic() {
  const [index, setIndex] = React.useState(0);

  const { data } = useCurrentTopicQuery();
  const navigate = useNavigate();

  if (!data?.current_topic) {
    return null;
  }

  return (
    <Box>
      <Collapse in={!!data} timeout={1200}>
        <Box
          sx={(theme) => ({
            display: "flex",
            alignItems: "flex-end",
            padding: theme.spacing(4, 0),
          })}
        >
          <Box
            sx={{
              flex: 1,
              overflow: "hidden",
            }}
          >
            <Box>
              <Box mb={3}>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <Typography variant="h5" gutterBottom>
                  {data?.current_topic.title}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  {data?.current_topic.description}
                </Typography>
              </Box>
              <Box display="flex" alignItems="center">
                {(() => {
                  const top_shadows = data?.current_topic.top_shadows || [];
                  if (top_shadows.length > 2) {
                    return (
                      <React.Fragment>
                        <div>
                          <IconButton
                            size="small"
                            onClick={() => setIndex(index - 1)}
                          >
                            <KeyboardArrowLeft />
                          </IconButton>
                        </div>
                        <Box sx={{ flex: 1, overflow: "hidden" }}>
                          <VirtualizeSwipeableViews
                            style={{ paddingRight: "50%" }}
                            index={index}
                            onChangeIndex={(index: number) => setIndex(index)}
                            enableMouseEvents
                            slideRenderer={(params) => {
                              const { key, index } = params;
                              const items =
                                data?.current_topic.top_shadows || [];
                              const idx = mod(index, items.length);
                              let shadow = items[idx];
                              if (shadow) {
                                return (
                                  <Box
                                    key={key}
                                    sx={(theme) => ({
                                      minHeight: 200,
                                      margin: theme.spacing(0, 0.25),
                                      display: "flex",
                                      alignItems: "center",
                                      justifyContent: "center",
                                      overflow: "hidden",
                                    })}
                                  >
                                    <MediaNormal
                                      {...items[idx]}
                                      onClickRoot={() =>
                                        navigate(`/shadow/${shadow.id}`)
                                      }
                                      onClickAuthor={() =>
                                        navigate(
                                          `/profile/${shadow.author.username}`
                                        )
                                      }
                                    />
                                  </Box>
                                );
                              }
                            }}
                          />
                        </Box>
                        <div>
                          <IconButton
                            size="small"
                            onClick={() => setIndex(index + 1)}
                          >
                            <KeyboardArrowRight />
                          </IconButton>
                        </div>
                      </React.Fragment>
                    );
                  } else if (
                    top_shadows.length > 0 &&
                    top_shadows.length <= 2
                  ) {
                    return (
                      <React.Fragment>
                        <Box
                          sx={{
                            flex: 1,
                            overflow: "hidden",
                            display: "flex",
                          }}
                        >
                          {top_shadows.map((item, idx) => {
                            return (
                              <Box
                                key={idx}
                                sx={(theme) => ({
                                  minHeight: 200,
                                  margin: theme.spacing(0, 0.25),
                                  display: "flex",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  overflow: "hidden",
                                  flex: 1,
                                })}
                              >
                                <MediaNormal
                                  {...item}
                                  onClickRoot={() =>
                                    navigate(`/shadow/${item.id}`)
                                  }
                                  onClickAuthor={() =>
                                    navigate(`/profile/${item.author.username}`)
                                  }
                                />
                              </Box>
                            );
                          })}
                          {top_shadows.length === 1 && (
                            <Box
                              sx={(theme) => ({
                                minHeight: 200,
                                margin: theme.spacing(0, 0.25),
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                overflow: "hidden",
                                flex: 1,
                              })}
                            >
                              <SpecialBox
                                placeholder
                                placeholderTitle=""
                              />
                            </Box>
                          )}
                        </Box>
                      </React.Fragment>
                    );
                  } else {
                    return <Placeholder subtitle="" />;
                  }
                })()}
              </Box>
            </Box>
          </Box>
          <Box
            sx={(theme) => ({
              flex: 1,
              overflow: "hidden",
              marginLeft: theme.spacing(4),
            })}
          >
            {data?.current_topic.top_shadow ? (
              <MediaNormal
                showDescription
                {...data?.current_topic.top_shadow}
                onClickRoot={() =>
                  navigate(`/shadow/${data?.current_topic?.top_shadow?.id}`)
                }
                onClickAuthor={() =>
                  navigate(
                    `/profile/${data?.current_topic?.top_shadow?.author?.username}`
                  )
                }
              />
            ) : (
              <Placeholder subtitle="" />
            )}
          </Box>
        </Box>
        <Divider />
      </Collapse>
    </Box>
  );
}

export default Topic;
