import * as React from "react";
import { styled } from "@mui/material/styles";
import { Box, Typography } from "@mui/material";
import Image from "@/components/base/image";
import { MediaOwnProps } from "./media";
import Duration from "../duration";
import { DEFULAT_SHADOW_COVER } from "@/common/constants/default.constant";
// import moment from "moment";

const PREFIX = "MediaOwn";

const classes = {
  wrap: `${PREFIX}-wrap`,
  cover: `${PREFIX}-cover`,
  duration: `${PREFIX}-duration`,
};

const StyledBox = styled(Box)(() => ({
  [`&.${classes.wrap}`]: {
    width: "100%",
    cursor: "pointer",
  },

  [`& .${classes.cover}`]: {
    position: "relative",
  },

  [`& .${classes.duration}`]: {
    position: "absolute",
    left: 10,
    bottom: 10,
  },
}));

export const MediaOwn: React.FC<MediaOwnProps> = (props: MediaOwnProps) => {
  const handleClickRoot = (e: React.MouseEvent) => {
    e.stopPropagation();
    props.onClickRoot && props.onClickRoot(e);
  };

  return (
    <StyledBox className={classes.wrap} onClick={handleClickRoot}>
      <div className={classes.cover}>
        <Image src={props.cover || DEFULAT_SHADOW_COVER} aspectRatio={16 / 9} />
        {props.duration && (
          <Duration
            classes={{ root: classes.duration }}
            duration={props.duration}
          />
        )}
      </div>
      <Box padding={1}>
        <Typography variant="subtitle1" noWrap>
          {props.title || props.name}
        </Typography>
        <Box display="flex" justifyContent="space-between" alignItems="center">
          {/* <Typography variant="caption" color="textSecondary">
            TODO
          </Typography> */}
          {/* <Typography variant="caption" color="textSecondary">
            {moment(props.create_at).format('YYYY-MM-DD')}
          </Typography> */}
        </Box>
      </Box>
    </StyledBox>
  );
};

export default MediaOwn;
